import { useState, useEffect, Suspense, lazy } from 'react';
import { produce } from 'immer';
import Data from './data';
const Karussell = lazy(() => import('./karussell'));

function KarussellContainer () {
    const pics = Data();
    const [image, setImage] = useState(pics[0]);
    const [ctrl, setCtrl] = useState(() => {
        const arr = new Array();
        arr.push(true);
        for (let i = 1; i < pics.length; i++) {
            arr.push(false);
        }
        return arr;
    });
    const [test, setTest] = useState();
    const [test2, setTest2] = useState();

    useEffect(() => {
        const v = setInterval(() => {
                auto();
        }, 1000 * 3)
        setTest(v);
        
      
        return () => clearInterval(v);
      }, []);

    function handleSwitch (i) {
        clearInterval(test);
        clearTimeout(test2);
        setCtrl((prevState) => {
            return produce(prevState, (draftState) => {
                for (let j = 0; j < draftState.length; j++) {
                    if (draftState[j] === true){
                        draftState[j] = false;
                        break;
                    }
                }
                draftState[i] = true;
            });
        })
        setImage(pics[i]);
        const time = setTimeout(() => {
            const v = setInterval(() => {
                    auto();
            }, 1000 * 3)
            setTest(v);
        }, 1000 * 10);
        setTest2(time);
    }

    function auto () {
        setCtrl((prevState) => {
            return produce(prevState, (draftState) => {
                for (let j = 0; j < draftState.length; j++) {
                    if (draftState[j] === true){
                        draftState[j] = false;
                        if (j < draftState.length-1) {
                            draftState[j+1] = true;
                            setImage(pics[j+1]);
                        }
                        else {
                            draftState[0] = true;
                            setImage(pics[0]);
                        }
                        break;
                    }
                }
            });
        })
    }

    
    return <Suspense><Karussell ctrl={ctrl} handleSwitch={handleSwitch} image={image} /></Suspense>;
    
}

export default KarussellContainer;